@media screen and (min-width: 320px) and (max-width: 767px) {
  .backdrop {
    position: fixed;
    overflow-y: scroll;
    top: 20px;
    left: 20px;
    width: 280px;
    height: 958px;
    background-color: #ffffff;
    padding: 48px 20px 41px 20px;
    z-index: 1;
    opacity: 1;
    transition: $shift;
  }
  .backdrop::-webkit-scrollbar {
    display: none;
  }

  .is-hidden {
    opacity: 0;
    pointer-events: none;
  }

  .close-btn {
    align-items: center;
    justify-content: center;
    border: none;
    position: absolute;
    top: 18px;
    right: 18px;
    width: 14px;
    height: 14px;
    background-color: #ffffff;
    fill: #000000;
    cursor: pointer;
    transition-property: fill;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  }
}

