.watched-btn {
  transition: all 1s;
}
.watched-btn:disabled {
  background: repeating-linear-gradient(45deg, #8c8c8c, #8c8c8c 10px, #393939 10px, #393939 20px);
}

.queue-btn {
  transition: all 1s;
}
.queue-btn:disabled {
  background-color: #8c8c8c;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .modal__pic,
  .modal__description,
  .description__vote,
  .description__popularity,
  .description__title,
  .description__genre,
  .description__btn {
    list-style: none;
    padding-left: 0;
  }
  .description__list,
  .description__statistic {
    list-style: none;
  }

  .description__vote,
  .description__popularity,
  .description__title,
  .description__genre,
  .description__btn {
    display: flex;
    list-style: none;
    padding-left: 0;
  }

  img {
    border-radius: 5px;
  }
  .description__film {
    font-family: 'Roboto';
    font-weight: 500;
    font-size: 20px;
    line-height: 23.44px;
    color: #000000;
    padding-bottom: 20px;
    padding-top: 30.43px;
  }

  .description__vote,
  .description__popularity,
  .description__title,
  .description__header {
    padding-bottom: 10px;
  }
  .description__genre {
    padding-bottom: 20px;
  }
  .vote__value {
    padding-left: 41px;
  }
  .popularity__value {
    padding-left: 53px;
  }
  .title__value {
    padding-left: 39px;
  }
  .genre__value {
    padding-left: 76px;
  }
  .vote__mark {
    width: 36px;
    height: 16px;
    padding-left: 10px;
    padding-right: 9px;
    background-color: #ff6b01;
    border-radius: 5px;
    font-family: 'Roboto';

    font-weight: 500;
    font-size: 12px;
    line-height: 14px;

    align-items: center;
    text-align: center;
    text-transform: uppercase;
    color: #ffffff;
  }

  .votes__mark {
    width: 36px;
    height: 16px;
    padding-left: 4px;
    padding-right: 4px;
    background-color: #f7f7f7;
    border-radius: 5px;
    font-family: 'Roboto';

    font-weight: 500;
    font-size: 12px;
    line-height: 14px;

    align-items: center;
    text-align: center;
    text-transform: uppercase;
    color: #000000;
  }

  .vote__text,
  .popularity__text,
  .title__text,
  .genre__text {
    font-family: 'Roboto';
    font-weight: 500;
    font-size: 12px;
    line-height: 1.33;
    color: #8c8c8c;
  }

  .vote__value,
  .popularity__value,
  .title__value,
  .genre__value {
    font-family: 'Roboto';
    font-weight: 500;
    font-size: 12px;
    line-height: 1.33;
    color: #000000;
  }

  .description__text {
    width: 240px;
    height: 236px;
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.67;
    color: #000000;
  }

  .watched-btn {
    width: 112.58px;
    height: 44px;
    background: #ff6b01;
    border-radius: 5px;
    font-family: 'Roboto';
    font-weight: 500;
    font-size: 12px;
    line-height: 1.33;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    text-transform: uppercase;
    color: #ffffff;
    margin-top: 21px;
    cursor: pointer;
  }

  .queue-btn {
    width: 112.58px;
    height: 44px;
    background-color: #ffffff;
    border: 1px solid #000000;
    border-radius: 5px;
    font-family: 'Roboto';
    font-weight: 500;
    font-size: 12px;
    line-height: 1.33;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    text-transform: uppercase;
    color: #000000;
    margin-left: 14.85px;
    margin-top: 21px;
    cursor: pointer;
  }
}

